import React from 'react';
import { NavLink } from 'react-router-dom';
import { Link as ScrollLink } from 'react-scroll'
import styled from 'styled-components';
import { FaTimes } from 'react-icons/fa'

const Sidebar = ({ isOpen, onToggle }) => {
  return (
    <SidebarContainer isOpen={isOpen}>
      <SidebarToggle onClick={onToggle}>
        <FaTimes />
      </SidebarToggle>
      <SidebarMenu isOpen={isOpen}>
          <li><NavLink exact to="/wesley-resvervation/" onClick={onToggle}>집회신청</NavLink></li>
          <li><ScrollLink to="info" onClick={onToggle}>집회안내</ScrollLink></li>
          <li><NavLink exact to="/wesley-resvervation/confirm" activeClassName="active" onClick={onToggle}>신청결과확인</NavLink></li>
        </SidebarMenu>
    </SidebarContainer>
  );
}

const SidebarContainer = styled.aside`
  position: fixed;
  z-index: 2001;
  width: 100%;
  height: 100%;
  background: #6F9695;
  display: grid;
  align-items: flex-start;
  justify-content: center;
  top: 0;
  left: 0;
  padding: 15rem 0;
  transition: 0.3s ease-in-out;
  opacity: ${({ isOpen }) => (isOpen ? '100%' : '0')};
  top: ${({ isOpen }) => (isOpen ? '0' : '-100%')};
`;

const SidebarToggle = styled.div`
  color: #fff;
  position: absolute;
  top: 2rem;
  right: 2rem;
  min-height: 2rem;
  min-width: 2rem;
  font-size: 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  cursor: pointer;
  outline: none;
`;

const SidebarMenu = styled.ul`
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 80px);
  text-align: center;
  
  li {
    font-size: 2rem;
    font-weight: bolder;

    a {
      color: #fff;
      text-decoration: none;
    }
  }
`;


export default Sidebar;