import React, {useState} from 'react';

import styled from 'styled-components';
import { Section } from '../../styles/styles'

import { db } from '../../config/firbaseConfig'

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const CheckSection = () => {
  const [ userlist, setUserlist ] = useState(false)

  const onClickLookUp = (title) => {
    db.collection(title).orderBy("submitTime").get().then((querySnapshot) => {
      let tempArray = []
       querySnapshot.forEach((doc) => {
         tempArray.push({
          id: doc.id,
          ...doc.data()
         })
      })
      setUserlist(tempArray)
    }).catch(error => {
      console.log(error)
    })
  }

  const converTime = (timestamp) => {
    let date = new Date(timestamp * 1000)
    let hours = date.getHours();
    let minutes = "0" + date.getMinutes();
    let seconds = "0" + date.getSeconds();
    let formattedTime = hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
    return formattedTime
  }

  return (
    <CheckContainer>
      <LookupContainer>
        <LookupButton onClick={() => onClickLookUp('firstday')}>첫째날</LookupButton>
        <LookupButton onClick={() => onClickLookUp('secondday')}>둘째날</LookupButton>
      </LookupContainer>
      
      <TableContainer component={Paper}>
        <Table aria-label="simple table" style={{minWidth: 250}}>
          <TableHead>
            <TableRow>
              <TableCell align="center">순서</TableCell>
              <TableCell align="center">교회</TableCell>
              <TableCell align="center">이름</TableCell>
              <TableCell align="center">직분</TableCell>
              <TableCell align="center">시간</TableCell>
            </TableRow>
          </TableHead>
          {userlist && (
            <>
            {userlist.map((user, index) => (
              <TableBody>
                <TableRow key={index}>
                  <TableCell align="center">{index}</TableCell>
                  <TableCell align="center">{user.church}</TableCell>
                  <TableCell align="center">{user.name}</TableCell>
                  <TableCell align="center">{user.position}</TableCell>
                  <TableCell align="center">{converTime(user.submitTime)}</TableCell>
                </TableRow>
              </TableBody>
            ))}  
            </>
          )}
        </Table>
      </TableContainer>
    </CheckContainer>
  );
}

const CheckContainer = styled(Section)`
  min-height: 88vh;
  width: 100%;
  margin-top: 3rem;
  justify-content: flex-start;
  align-items: flex-start;

  @media screen and (min-width: 768px) {
    padding: 0 10rem;
    max-width: 768px;
  }
`;

const LookupContainer = styled.div`
  margin-bottom: 1rem;
`;

const LookupButton = styled.button`
  border: 2px solid #228be6;
  border-radius: 10px;
  padding: 1rem 1.6rem;
  margin: 0 1rem;
  background: #fff;
`;

export default CheckSection;