import React from 'react';
import { useForm } from "react-hook-form";
import styled from 'styled-components'
import { Section } from '../../styles/styles'
import { auth } from '../../config/firbaseConfig'

const SignUpSection = ({ setIsLoggedIn }) => {
  const { register, handleSubmit } = useForm()

  const onSubmit = async (data) => {
    try {
      await auth.signInWithEmailAndPassword(data.email, data.password)
      console.log("LOGIN SUCCESS")

    } catch (error) {
      console.log("Error @SignIn: ", error)
    }
    setIsLoggedIn(true)
  }

  return (
    <SignUpContainer>
      <FormTitle>관리자 로그인</FormTitle>
      <FormContainer onSubmit={handleSubmit(onSubmit)}>
        <InputContainer>
          <label htmlFor="email" className="label-email">이메일</label>
          <input 
            type="email" 
            name="email" 
            autoComplete="off" 
            className="input-email" 
            {...register('email', { required: true })}
          />
        </InputContainer>
        <InputContainer>
          <label htmlFor="password" className="label-password">비밀번호</label>
          <input 
            type="password" 
            name="password" 
            autoComplete="off" 
            className="input-password"
            {...register('password', { required: true })}
          />
        </InputContainer>
        <ButtonContainer>
          <button type="submit">로그인</button>
        </ButtonContainer>
      </FormContainer>
    </SignUpContainer>
  );
}

const SignUpContainer = styled(Section)``

const FormTitle = styled.p`
  color: #1e1e23;
  letter-spacing: -0.3px;
  font-size: 2.2rem;
  margin: 3rem 0;
`;

const FormContainer = styled.form`
  display: block;
  width: 80%;

  @media screen and (min-width: 1180px) {
    max-width: 768px;
  }
`;

const InputContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 2.4rem;

  label {
    font-size: 1.6rem;
    padding: 0 0 0.3rem;
  }
  input {
    padding : 1.5rem 0.75rem;
    font-size: 1.6rem;
    border: 2px solid rgb(101, 177, 177);
    border-radius: 4px;
    outline: none
  }
`;

const ButtonContainer = styled.div`
  margin-top: 4rem;

  button {
    width: 100%;
    padding: 1.6rem 2rem;
    background: rgb(101, 177, 177);
    border: none;
    border-radius: 4px;
    color: #fff;
    font-size: 1.8rem;
  }
`;

export default SignUpSection;