import React from 'react';
import { NavLink } from 'react-router-dom';
import { animateScroll, Link as ScrollLink } from 'react-scroll'
import styled from 'styled-components';
import { FaBars } from 'react-icons/fa'

const NavBar = ({ onToggle}) => {

  const toggleTop = () => {
    animateScroll.scrollToTop()
  }

  return (
      <Navbar>
        <NavbarLogo>
          <h1 className="logo">
            <NavLink to="/wesley-resvervation/" onClick={toggleTop}>
              283주년 웨슬리회심기념 <br/>
              온라인연합성회
            </NavLink>
          </h1>
        </NavbarLogo>
        <NavbarMenu>
          <li><NavLink exact to="/wesley-resvervation/" activeClassName="active">집회신청</NavLink></li>
          <li><ScrollLink to="info">집회안내</ScrollLink></li>
          <li><NavLink exact to="/wesley-resvervation/confirm" activeClassName="active">신청결과확인</NavLink></li>
        </NavbarMenu>
        <NavBarToggle onClick={onToggle}>
          <FaBars />
        </NavBarToggle>
      </Navbar>
  );
}

