const Navbar = styled.nav`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  min-height: 6vh;
  z-index: 2000;
  position: sticky;
  top: 0;
  padding: 1rem;
  width: 100%;

  @media screen and (min-width: 768px) {
    transition: 0.8s all ease;
    padding: 1rem 3rem;
  }

  @media screen and (min-width: 1180px) {
    transition: 0.8s all ease;
    padding: 1rem 10rem;
  }
`;

const NavbarLogo = styled.div`
  margin-left: 1rem;

  .logo {
    font-size: 1.8rem;
    font-family: 'Jua', sans-serif;
    font-weight: lighter;
    
    a {
      color: #1e1e23;
      text-decoration: none;
    }
  }

  @media screen and (min-width: 768px) {
    margin-left: 0;
  }

`;

const NavbarMenu = styled.ul`
  display: none;

  @media screen and (min-width: 768px) {
    display: flex;

    li {
      margin-left: 2.4rem;

      &:active {
        box-shadow: inset 0 -2px 0 #000;
        font-weight: 700;
      }

      a {
        display: flex;
        align-items: center;
        min-height: 60px;
        color: #1e1e23;
        font-size: 1.6rem;
      }
    }
  }
`;

const NavBarToggle = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 2rem;
  min-height: 2rem;
  border: 0;
  background: transparent;
  cursor: pointer;
  font-size: 1.6rem;
  margin-right: 1rem;

  @media screen and (min-width: 768px) {
    display: none;
  }
`;


export default NavBar;