import React, { useState } from 'react';
import { Route, Switch, BrowserRouter } from 'react-router-dom'

import { InfoProvider } from './context/InfoContext';

import GlobalStyle from '../src/styles/GlobalStyle';
import NavBar from './components/NavBar';
import Sidebar from './components/Sidebar';
import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import RegisterPage from './pages/RegisterPage';
import ResultPage from './pages/ResultPage';
import ConfirmPage from './pages/ConfirmPage';
import AdminPage from './pages/AdminPage';
import ScrollToTop from './components/ScrollToTop';


function App() {
  const [ isOpen, setIsOpen ] = useState(false)

  const onToggle = () => {
    setIsOpen(!isOpen)
  }

  return (
    <InfoProvider>
      <BrowserRouter basename={'/'}>
        <GlobalStyle />
        <ScrollToTop />
        <NavBar onToggle={onToggle}/>
        <Sidebar isOpen={isOpen} onToggle={onToggle}/>
        <Switch>
          <Route exact path="/wesley-resvervation/" component={HomePage} />
          <Route exact path="/wesley-resvervation/register" component={RegisterPage} />
          <Route exact path="/wesley-resvervation/result" component={ResultPage} />
          <Route exact path="/wesley-resvervation/confirm" component={ConfirmPage} />
          <Route exact path="/wesley-resvervation/admin" component={AdminPage} />
        </Switch>
        <Footer />
      </BrowserRouter>
    </InfoProvider>
  );
}

export default App;
