import React, { useContext } from 'react';
import { db } from '../../config/firbaseConfig'
import { InfoContext } from '../../context/InfoContext'

import styled from 'styled-components';

const ConfirmSection = ({ setGetResult, setIsChecking }) => {
  const [ registerInfo ] = useContext(InfoContext)

  
  const onSubmit = () => {
    const userListRef = db.collection(registerInfo.session)
    let indexQuery = userListRef.where("index", "==", 120)
    let query = userListRef.where("name", "==", registerInfo.name).where("church", "==", registerInfo.church);
    query.get().then(querySnapshot => {
      if (querySnapshot.empty) {
        console.log('No register data')
        setGetResult({
          session: registerInfo.session,
          name: registerInfo.name,
          church: registerInfo.church,
          result: false
        })
      } else {
        querySnapshot.forEach((doc) => {
          if (doc.data().index < 120) {
            console.log('선착순 당첨')
            setGetResult({
              session: registerInfo.session,
              name: registerInfo.name,
              church: registerInfo.church,
              result: true
            })
          } else if (doc.data().index === 120) {
            indexQuery.get().then((querySnapshot) => {
              let tempArray = []
              querySnapshot.forEach((doc, idx) => {
                tempArray.push({
                  id: doc.id,
                  order: idx,
                  ...doc.data()
                })
              })
              tempArray.sort((a, b) => {
                return a.submitTime - b.submitTime
              })
              if (tempArray[0].name === registerInfo.name) {
                console.log('선착순당첨')
                setGetResult({
                  session: registerInfo.session,
                  name: registerInfo.name,
                  church: registerInfo.church,
                  result: true
                })
              } else {
                console.log('120명 이후 신청자')
                setGetResult({
                  session: registerInfo.session,
                  name: registerInfo.name,
                  church: registerInfo.church,
                  result: false
                })
              }
            })
          } 
          else if (doc.data().index > 120) {
            setGetResult({
              session: registerInfo.session,
              name: registerInfo.name,
              church: registerInfo.church,
              result: false
            })
          }
        })
      }
    })
    setIsChecking(true)
  }

  return (
    <ConfirmContainer>
      <Title>신청해주셔서 감사합니다</Title>
      <Subtitle>신청하신 결과는 아래에서 확인해주세요</Subtitle>
      <ButtonContainer>
          <button type="submit" onClick={() => onSubmit()}>결과확인</button>
        </ButtonContainer>
    </ConfirmContainer>
  );
}

const ConfirmContainer = styled.div`
  margin-top: 7rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: 768px) {
    max-width: 768px;
  }
`;

const Title = styled.h1`
  font-size: 2.2rem;
  color: blue;
  padding: 2.4rem 0;

  @media screen and (min-width: 768px) {
    font-size: 2.6rem;
  }
`;

const Subtitle = styled.h3`
  font-size: 1.6rem;

  @media screen and (min-width: 768px) {
    font-size: 2.0rem;
  }
`;

const ButtonContainer = styled.div`
  margin-top: 4rem;

  button {
    width: 100%;
    padding: 1.6rem 2rem;
    background: rgb(101, 177, 177);
    border: none;
    border-radius: 4px;
    color: #fff;
    font-size: 1.8rem;
  }

  @media screen and (min-width: 768px) {
    
    button {
      width: 100%;
      padding: 2rem 3.2rem;
      font-size: 2.4rem;
    }
  }
`;

export default ConfirmSection;